// voiceRecognitionService.ts
export class VoiceRecognitionService {
  private static instance: VoiceRecognitionService;
  private recognition: any = null;
  private isListening: boolean = false;

  private constructor() {
    // Check if browser supports speech recognition
    const SpeechRecognition = (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;
    
    if (SpeechRecognition) {
      this.recognition = new SpeechRecognition();
      this.recognition.lang = 'ar-SA';
      this.recognition.interimResults = false;
      this.recognition.maxAlternatives = 1;
      this.recognition.continuous = false;
    } else {
      console.warn('Speech recognition not supported in this browser');
    }
  }

  public static getInstance(): VoiceRecognitionService {
    if (!VoiceRecognitionService.instance) {
      VoiceRecognitionService.instance = new VoiceRecognitionService();
    }
    return VoiceRecognitionService.instance;
  }

  async startListening(): Promise<string> {
    if (!this.recognition) {
      throw new Error('Speech recognition not supported in this browser');
    }

    // Prevent multiple starts
    if (this.isListening) {
      throw new Error('Speech recognition is already running');
    }

    return new Promise((resolve, reject) => {
      let timeoutId: any = null;
      let hasResult = false;
      
      this.recognition.onresult = (event: any) => {
        if (timeoutId) clearTimeout(timeoutId);
        
        // Process the final result
        for (let i = event.resultIndex; i < event.results.length; i++) {
          if (event.results[i].isFinal) {
            const transcript = event.results[i][0].transcript;
            console.log('Speech recognition result:', transcript);
            hasResult = true;
            this.isListening = false;
            resolve(transcript);
            break;
          }
        }
      };

      this.recognition.onerror = (event: any) => {
        if (timeoutId) clearTimeout(timeoutId);
        this.isListening = false;
        console.error('Speech recognition error:', event.error, event);
        
        // Handle specific error types
        switch (event.error) {
          case 'no-speech':
            // Even if no-speech error, check if we got any results
            if (hasResult) {
              // If we have results, resolve with empty string
              resolve('');
            } else {
              reject(new Error('    .         .'));
            }
            break;
          case 'audio-capture':
            reject(new Error('     .     .'));
            break;
          case 'not-allowed':
            reject(new Error('    .      .'));
            break;
          case 'network':
            reject(new Error('  .     .'));
            break;
          default:
            reject(new Error(`    : ${event.error}`));
        }
      };

      this.recognition.onend = () => {
        if (timeoutId) clearTimeout(timeoutId);
        // If recognition ends without result and no error, resolve with empty string
        if (!hasResult && this.isListening) {
          this.isListening = false;
          resolve('');
        }
      };

      try {
        // Set a timeout to automatically stop listening after 15 seconds
        timeoutId = setTimeout(() => {
          console.log('Speech recognition timeout reached');
          this.stopListening();
          if (!hasResult) {
            reject(new Error('  .        .'));
          }
        }, 15000);
        
        this.recognition.start();
        this.isListening = true;
        console.log('Speech recognition started');
      } catch (error) {
        if (timeoutId) clearTimeout(timeoutId);
        this.isListening = false;
        console.error('Error starting speech recognition:', error);
        reject(error);
      }
    });
  }

  stopListening(): void {
    if (this.recognition && this.isListening) {
      this.recognition.stop();
      this.isListening = false;
      console.log('Speech recognition stopped');
    }
  }

  isAvailable(): boolean {
    return !!this.recognition;
  }
  
  // Add method to check if currently listening
  getIsListening(): boolean {
    return this.isListening;
  }
}