// VoiceInterface.tsx
import React, { useState, useEffect, useRef, useCallback } from 'react';
import { GoogleAIService } from '../services/googleAIService';
import { ElevenLabsService } from '../services/elevenLabsService';
import { AmazonPollyService } from '../services/amazonPollyService';
import { VoiceRecognitionService } from '../services/voiceRecognitionService';

const VoiceInterface: React.FC = () => {
  const [isListening, setIsListening] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [statusMessage, setStatusMessage] = useState('   ');
  const [conversationActive, setConversationActive] = useState(false);
  const [conversationHistory, setConversationHistory] = useState<Array<{type: 'user' | 'ai', message: string}>>([]);
  const isMountedRef = useRef(true);
  const isStartingRef = useRef(false); // Prevent multiple start attempts
  const timeoutRef = useRef<NodeJS.Timeout | null>(null); // Store timeout reference

  const googleAIService = GoogleAIService.getInstance();
  const elevenLabsService = ElevenLabsService.getInstance();
  const amazonPollyService = AmazonPollyService.getInstance();
  const voiceRecognitionService = VoiceRecognitionService.getInstance();

  const checkBrowserSupport = useCallback(() => {
    const SpeechRecognition = (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;
    if (!SpeechRecognition) {
      updateStatus('     .   Chrome  Edge.');
    }
  }, []);

  // Clean up on unmount
  useEffect(() => {
    // Check browser support on mount
    checkBrowserSupport();

    // Cleanup function
    return () => {
      isMountedRef.current = false;

      // Stop any playing audio
      elevenLabsService.stopAudio();

      // Clear any pending timeouts
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
        timeoutRef.current = null;
      }
    };
  }, [checkBrowserSupport, elevenLabsService]);

  const updateStatus = (message: string) => {
    // Always update status regardless of mount state for immediate feedback
    setStatusMessage(message);
  };
  
  const welcomeUser = async () => {
    const welcomeMessage = "    !    .";

    // Check if Amazon Polly is available
    const isPollyAvailable = amazonPollyService.isAvailable();

    try {
      if (isPollyAvailable) {
        const audioBuffer = await amazonPollyService.textToSpeech(welcomeMessage, 'Zayd');
        if (audioBuffer && audioBuffer.byteLength > 0) {
          // Set speaking state before playing audio
          if (isMountedRef.current) {
            setIsSpeaking(true);
          }
          await amazonPollyService.playAudio(audioBuffer);
        } else {
          await speakText(welcomeMessage);
        }
      } else {
        await speakText(welcomeMessage);
      }
    } catch (error) {
      console.error('Error in welcomeUser:', error);
      // Fallback to browser TTS even if Polly fails
      await speakText(welcomeMessage);
    } finally {
      // Reset speaking state
      if (isMountedRef.current) {
        setIsSpeaking(false);
      }
    }

    // Start listening after welcome message
    setTimeout(() => {
      if (isMountedRef.current) {
        startListening();
      }
    }, 2000);
  };

  const startListening = async () => {
    // Prevent multiple start attempts
    if (isStartingRef.current || isListening || voiceRecognitionService.getIsListening()) {
      return;
    }

    if (!voiceRecognitionService.isAvailable()) {
      updateStatus('     ');
      return;
    }

    // Start continuous conversation mode
    if (!conversationActive) {
      setConversationActive(true);
      // Start with welcome message when conversation begins
      await welcomeUser();
      return; // Exit here, welcomeUser will start listening after
    }

    try {
      isStartingRef.current = true;
      setIsListening(true);
      updateStatus(' ...    ');
      const transcript = await voiceRecognitionService.startListening();
      setIsListening(false);
      isStartingRef.current = false;

      // Check if we got a valid transcript
      if (transcript && transcript.trim()) {
        const cleanTranscript = transcript.trim();

        // Add user message to conversation history
        setConversationHistory(prev => [...prev, { type: 'user', message: cleanTranscript }]);

        // Check for conversation end commands
        const endCommands = ['', '', '', '', '', '', '', '', '', ''];
        const shouldEndConversation = endCommands.some(cmd => cleanTranscript.toLowerCase().includes(cmd));

        if (shouldEndConversation && conversationActive) {
          setConversationActive(false);
      // Stop any ongoing audio playback
      elevenLabsService.stopAudio();
      amazonPollyService.stopAudio();
          updateStatus('  .      .');
          return;
        }

        // Clear any existing timeout
        if (timeoutRef.current) {
          clearTimeout(timeoutRef.current);
        }

        // Use a more robust timeout approach
        timeoutRef.current = setTimeout(() => {
          processVoice(cleanTranscript);
        }, 500);
      } else {
        updateStatus('    .       .');
        setTimeout(() => {
          updateStatus('   ');
        }, 3000);
      }
    } catch (error: any) {
      console.error('Voice recognition error:', error);
      isStartingRef.current = false;
      setIsListening(false);
      updateStatus(error.message || '   ');

      // Automatically reset status after error
      setTimeout(() => {
        updateStatus('   ');
      }, 4000);
    }
  };

  const stopListening = () => {
    isStartingRef.current = false;

    // Clear any pending timeout
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = null;
    }

    voiceRecognitionService.stopListening();
    setIsListening(false);
    updateStatus('  ');
  };

  const endConversation = () => {
    setConversationActive(false);
    // Stop any ongoing audio playback
    elevenLabsService.stopAudio();
    amazonPollyService.stopAudio();
    updateStatus('  .      .');
  };

  const processVoice = async (userInput: string) => {
    // Clear the timeout reference since we're now processing
    timeoutRef.current = null;

    // Even if component is unmounted, we should still try to process the voice
    // But we'll be careful about updating state

    if (!userInput || !userInput.trim()) {
      updateStatus('    ');
      return;
    }

    const cleanInput = userInput.trim();

    // Update state only if component is still mounted
    if (isMountedRef.current) {
      setIsProcessing(true);
    }
    updateStatus(' ...');

    try {
      // Get response from Google AI
      console.log('Sending request to Google AI...');
      const response = await googleAIService.generateResponse(cleanInput);
      console.log('Received response from Google AI:', response.substring(0, 100) + '...');
      
      // Use the response directly from Google AI, which now includes Saudi dialect
      let modifiedResponse = response;
      
      updateStatus('    ...');

      // Check TTS services availability in priority order: Polly > Browser
      const isPollyAvailable = amazonPollyService.isAvailable();
      console.log('TTS availability check - Polly:', isPollyAvailable);

      let audioBuffer: ArrayBuffer | null = null;
      let usedService = 'browser';

      try {
        if (isPollyAvailable) {
          console.log('Sending text to Amazon Polly TTS...');
          audioBuffer = await amazonPollyService.textToSpeech(modifiedResponse, 'Zayd');
          usedService = 'polly';
        }

        console.log(`${usedService} audio buffer received:`, !!audioBuffer, audioBuffer ? audioBuffer.byteLength : 0);

        if (audioBuffer && audioBuffer.byteLength > 0) {
          console.log(`Playing ${usedService} audio...`);
          // Update state only if component is still mounted
          if (isMountedRef.current) {
            setIsSpeaking(true);
          }
          // Add AI response to conversation history
          setConversationHistory(prev => [...prev, { type: 'ai', message: modifiedResponse }]);
          try {
            if (usedService === 'polly') {
              await amazonPollyService.playAudio(audioBuffer);
            }
            console.log('Audio playback completed successfully');
          } catch (playbackError) {
            console.error('Audio playback failed:', playbackError);
            // Fallback to browser TTS
            updateStatus('    ...');
            await speakText(modifiedResponse);
          } finally {
            // Update state only if component is still mounted
            if (isMountedRef.current) {
              setIsSpeaking(false);
              if (conversationActive) {
                updateStatus(' ...');
                // Continue listening for next question after a short delay
                setTimeout(() => {
                  if (conversationActive && isMountedRef.current) {
                    startListening();
                  }
                }, 500);
              } else {
                updateStatus('   ');
              }
            }
          }
        } else {
          console.log(`No audio buffer from ${usedService}, using fallback TTS`);
          // Fallback to browser TTS
          updateStatus('    ...');
          await speakText(modifiedResponse);
        }
      } catch (ttsError) {
        console.error('TTS service error:', ttsError);
        // Fallback to browser TTS
        updateStatus('    ...');
        await speakText(modifiedResponse);
      }
    } catch (error: any) {
      console.error('Processing error:', error);
      // Update state only if component is still mounted
      if (isMountedRef.current) {
        setIsSpeaking(false);
        setIsProcessing(false);
      }
      updateStatus('   : ' + (error.message || ''));
      
      // Automatically reset status after error
      setTimeout(() => {
        updateStatus('   ');
      }, 4000);
    } finally {
      // Update state only if component is still mounted
      if (isMountedRef.current) {
        setIsProcessing(false);
      }
    }
  };

  const speakText = (text: string): Promise<void> => {
    console.log('Using fallback browser TTS for:', text.substring(0, 50) + '...');
    
    return new Promise((resolve) => {
      if ('speechSynthesis' in window) {
        const utterance = new SpeechSynthesisUtterance(text);
        utterance.lang = 'ar-SA';
        utterance.rate = 1;
        utterance.pitch = 1;
        utterance.volume = 1;
        
        // Wait for voices to load
        if (speechSynthesis.getVoices().length === 0) {
          speechSynthesis.onvoiceschanged = () => {
            const voices = speechSynthesis.getVoices();
            const arabicVoice = voices.find(voice => 
              voice.lang.includes('ar') || voice.name.includes('Arabic')
            );
            
            if (arabicVoice) {
              utterance.voice = arabicVoice;
              console.log('Using Arabic voice:', arabicVoice.name);
            } else {
              console.log('No Arabic voice found, using default voice');
            }
            
            utterance.onend = () => {
              console.log('Browser TTS completed');
              // Update state only if component is still mounted
              if (isMountedRef.current) {
                setIsSpeaking(false);
              }
              resolve();
            };
            
            utterance.onerror = (event) => {
              console.error('Speech synthesis error:', event);
              // Update state only if component is still mounted
              if (isMountedRef.current) {
                setIsSpeaking(false);
              }
              resolve();
            };
            
            console.log('Speaking with browser TTS...');
            speechSynthesis.speak(utterance);
          };
        } else {
          // Voices already loaded
          const voices = speechSynthesis.getVoices();
          const arabicVoice = voices.find(voice => 
            voice.lang.includes('ar') || voice.name.includes('Arabic')
          );
          
          if (arabicVoice) {
            utterance.voice = arabicVoice;
            console.log('Using Arabic voice:', arabicVoice.name);
          } else {
            console.log('No Arabic voice found, using default voice');
          }
          
          utterance.onend = () => {
            console.log('Browser TTS completed');
            // Update state only if component is still mounted
            if (isMountedRef.current) {
              setIsSpeaking(false);
            }
            resolve();
          };
          
          utterance.onerror = (event) => {
            console.error('Speech synthesis error:', event);
            // Update state only if component is still mounted
            if (isMountedRef.current) {
              setIsSpeaking(false);
            }
            resolve();
          };
          
          console.log('Speaking with browser TTS...');
          speechSynthesis.speak(utterance);
        }
      } else {
        console.warn('Web Speech API is not supported in this browser');
        // Even if TTS fails, we need to resolve the promise
        // Update state only if component is still mounted
        if (isMountedRef.current) {
          setIsSpeaking(false);
        }
        resolve();
      }
    });
  };

  return (
    <div className="app-container">
      <header className="app-header">
        <h1 className="app-title"><span className="blood-icon"></span>    </h1>
      </header>

      <main className="microphone-container" id="microphoneContainer">
        <div className={`sound-wave ${isListening ? 'listening' : ''}`}></div>
        <div className={`sound-wave ${isListening ? 'listening' : ''}`}></div>
        <div className={`sound-wave ${isListening ? 'listening' : ''}`}></div>
        <div
          className={`microphone ${isListening ? 'listening' : ''}`}
          onClick={isListening ? stopListening : startListening}
        >
          {isListening && <div className="recording-indicator"></div>}
        </div>
        <div className={`status-message ${statusMessage ? 'show' : ''}`} id="statusMessage">
          {statusMessage}
        </div>
      </main>

      {/* Conversation History Box */}
      <div className="conversation-history">
        <h3> </h3>
        <div className="conversation-messages">
          {conversationHistory.map((message, index) => (
            <div key={index} className={`message ${message.type}`}>
              <strong>{message.type === 'user' ? ':' : ':'}</strong> {message.message}
            </div>
          ))}
        </div>
      </div>

      <footer className="app-footer">
        <button
          className="talk-button"
          id="talkButton"
          onClick={isListening ? stopListening : (conversationActive ? endConversation : startListening)}
          disabled={isProcessing || isSpeaking || isStartingRef.current}
        >
          {isListening ? ' ' : (conversationActive ? ' ' : ' ')}
        </button>
      </footer>
    </div>
  );
};

export default VoiceInterface;