// googleAIService.ts
import { GoogleGenerativeAI, GenerativeModel } from '@google/generative-ai';

export class GoogleAIService {
  private static instance: GoogleAIService;
  private genAI: GoogleGenerativeAI | null = null;
  private model: GenerativeModel | null = null;
  private apiKey: string | undefined;

  private constructor() {
    this.apiKey = process.env.REACT_APP_GOOGLE_API_KEY;
    console.log('Google API Key available:', !!this.apiKey);
    if (this.apiKey) {
      console.log('Google API Key length:', this.apiKey.length);
    }
    
    if (this.apiKey) {
      try {
        this.genAI = new GoogleGenerativeAI(this.apiKey);
        this.model = this.genAI.getGenerativeModel({ model: "gemini-2.5-flash" });
        console.log('Google Generative AI initialized successfully');
      } catch (err) {
        console.error('Error initializing Google Generative AI model:', err);
        this.model = null;
      }
    } else {
      console.warn('Google API key not found. AI features will be disabled.');
    }
  }

  public static getInstance(): GoogleAIService {
    if (!GoogleAIService.instance) {
      GoogleAIService.instance = new GoogleAIService();
    }
    return GoogleAIService.instance;
  }

  private getSystemPrompt(): string {
    return `      .         .

    :
-   : A, B, AB, O   Rh (+  -)
-      
-      18-60   
-     50   
-       
-        3    

  :
-  
-   
-    
-   
-   

        .     :
- ""   " "
- ""   " "
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""
- ""   ""

        .             .      .

                 :
- "    "
- "    "
- "    "
- "        "
- "      "
- "     "

           .     .

             .`;
  }

  async generateResponse(userMessage: string): Promise<string> {
    console.log('Generating response for:', userMessage);
    
    if (!this.model) {
      console.warn('Google Generative AI model not available');
      return '    .     .';
    }

    try {
      const prompt = `${this.getSystemPrompt()}\n\n: ${userMessage}\n:`;
      console.log('Sending prompt to Google AI:', prompt);
      
      const result = await this.model.generateContent(prompt);
      const response = result.response?.text();

      if (!response) {
        throw new Error('      ');
      }

      console.log('Received response from Google AI:', response);
      return response;
    } catch (error: any) {
      console.error('Google Generative AI Error:', error);
      return '     .    .';
    }
  }
}