// elevenLabsService.ts
export class ElevenLabsService {
  private static instance: ElevenLabsService;
  private apiKey: string | undefined;
  private baseUrl: string = 'https://api.elevenlabs.io/v1';
  private currentAudio: HTMLAudioElement | null = null;

  private constructor() {
    this.apiKey = process.env.REACT_APP_ELEVENLABS_API_KEY;
    console.log('ElevenLabs API Key available:', !!this.apiKey);
    if (this.apiKey) {
      console.log('ElevenLabs API Key length:', this.apiKey.length);
    }
  }

  public static getInstance(): ElevenLabsService {
    if (!ElevenLabsService.instance) {
      ElevenLabsService.instance = new ElevenLabsService();
    }
    return ElevenLabsService.instance;
  }

  /**
   * Stops any currently playing audio
   */
  public stopAudio(): void {
    if (this.currentAudio) {
      try {
        this.currentAudio.pause();
        this.currentAudio.dispatchEvent(new Event('ended'));
      } catch (e) {
        console.warn('Error stopping audio:', e);
      } finally {
        this.currentAudio = null;
      }
    }
  }

  async textToSpeech(text: string, voiceId: string = '3nav5pHC1EYvWOd5LmnA'): Promise<ArrayBuffer | null> {
    if (!this.apiKey) {
      console.warn('ElevenLabs API key not found. TTS features will be disabled.');
      return null;
    }

    try {
      console.log('Sending text to ElevenLabs:', text.substring(0, 50) + '...');
      console.log('Using voice ID:', voiceId);
      
      const response = await fetch(`${this.baseUrl}/text-to-speech/${voiceId}`, {
        method: 'POST',
        headers: {
          'xi-api-key': this.apiKey,
          'Content-Type': 'application/json',
          'Accept': 'audio/mpeg'
        },
        body: JSON.stringify({
          text: text,
          model_id: 'eleven_multilingual_v2',
          voice_settings: {
            stability: 0.15,
            similarity_boost: 0.65,
            style: 0.9,
            use_speaker_boost: true
          },
          pronunciation_dictionary_locator: null
        })
      });

      console.log('ElevenLabs API response status:', response.status);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error('ElevenLabs API error response:', errorText);
        throw new Error(`ElevenLabs API error: ${response.status} - ${errorText}`);
      }

      const contentType = response.headers.get('content-type');
      console.log('ElevenLabs API response content-type:', contentType);
      
      const arrayBuffer = await response.arrayBuffer();
      console.log('Received audio buffer from ElevenLabs, size:', arrayBuffer.byteLength);
      
      if (arrayBuffer.byteLength === 0) {
        console.warn('Received empty audio buffer from ElevenLabs');
        return null;
      }
      
      return arrayBuffer;
    } catch (error) {
      console.error('ElevenLabs TTS Error:', error);
      return null;
    }
  }

  async playAudio(arrayBuffer: ArrayBuffer): Promise<void> {
    // Stop any currently playing audio
    if (this.currentAudio) {
      try {
        // Properly stop the current audio
        this.currentAudio.pause();
        // Dispatch an ended event to clean up properly
        this.currentAudio.dispatchEvent(new Event('ended'));
      } catch (e) {
        console.warn('Error stopping current audio:', e);
      } finally {
        this.currentAudio = null;
      }
    }
    console.log('Attempting to play audio buffer, size:', arrayBuffer.byteLength);
    
    return new Promise((resolve, reject) => {
      try {
        // Check if we have a valid audio buffer
        if (arrayBuffer.byteLength === 0) {
          console.warn('Received empty audio buffer');
          reject(new Error('Received empty audio buffer'));
          return;
        }
        
        // Create an audio element for better compatibility
        const blob = new Blob([arrayBuffer], { type: 'audio/mpeg' });
        const url = URL.createObjectURL(blob);
        const audio = new Audio(url);
        
        // Store reference to current audio
        this.currentAudio = audio;
        
        console.log('Created audio element with blob URL');
        
        // Handle audio events
        audio.onended = () => {
          if (this.currentAudio === audio) {
            this.currentAudio = null;
          }
          URL.revokeObjectURL(url);
          console.log('Audio playback completed');
          resolve();
        };
        
        audio.onerror = (error) => {
          if (this.currentAudio === audio) {
            this.currentAudio = null;
          }
          URL.revokeObjectURL(url);
          console.error('Audio playback error:', error);
          reject(new Error('Failed to play audio'));
        };
        
        audio.oncanplay = () => {
          console.log('Audio can be played');
        };
        
        audio.onloadeddata = () => {
          console.log('Audio data loaded');
        };
        
        audio.onplay = () => {
          console.log('Audio play started');
        };
        
        // Play the audio
        console.log('Attempting to play audio...');
        const playPromise = audio.play();
        
        if (playPromise !== undefined) {
          playPromise.then(() => {
            console.log('Audio play started successfully');
          }).catch(error => {
            if (this.currentAudio === audio) {
              this.currentAudio = null;
            }
            URL.revokeObjectURL(url);
            console.error('Audio play error:', error);
            // Check if it's an abort error (harmless in our case)
            if (error.name === 'AbortError') {
              console.log('Audio play was aborted, likely due to new request');
              resolve(); // Resolve instead of rejecting for abort errors
            } else {
              reject(new Error('Failed to play audio: ' + error.message));
            }
          });
        }
      } catch (error) {
        console.error('Error playing audio:', error);
        if (this.currentAudio) {
          this.currentAudio = null;
        }
        reject(error);
      }
    });
  }
}