// amazonPollyService.ts
import * as AWS from 'aws-sdk';

export class AmazonPollyService {
  private static instance: AmazonPollyService;
  private polly: AWS.Polly | null = null;
  private currentAudio: HTMLAudioElement | null = null;

  private constructor() {
    const accessKeyId = process.env.REACT_APP_AWS_ACCESS_KEY_ID;
    const secretAccessKey = process.env.REACT_APP_AWS_SECRET_ACCESS_KEY;

    if (!accessKeyId || !secretAccessKey) {
      console.warn('AWS credentials not found. Polly TTS will be disabled.');
      return;
    }

    AWS.config.update({
      accessKeyId: accessKeyId,
      secretAccessKey: secretAccessKey,
      region: 'us-east-1' // You can change this to your preferred region
    });

    this.polly = new AWS.Polly();
    console.log('Amazon Polly service initialized');
  }

  public static getInstance(): AmazonPollyService {
    if (!AmazonPollyService.instance) {
      AmazonPollyService.instance = new AmazonPollyService();
    }
    return AmazonPollyService.instance;
  }

  /**
   * Checks if Polly service is available
   */
  public isAvailable(): boolean {
    return !!this.polly;
  }

  /**
   * Stops any currently playing audio
   */
  public stopAudio(): void {
    if (this.currentAudio) {
      try {
        this.currentAudio.pause();
        this.currentAudio.dispatchEvent(new Event('ended'));
      } catch (e) {
        console.warn('Error stopping audio:', e);
      } finally {
        this.currentAudio = null;
      }
    }
  }

  async textToSpeech(text: string, voiceId: string = 'Zeina'): Promise<ArrayBuffer | null> {
    if (!this.polly) {
      console.warn('Polly service not initialized');
      return null;
    }

    try {
      console.log('Sending text to Amazon Polly:', text.substring(0, 50) + '...');
      console.log('Using voice ID:', voiceId);

      const params = {
        Text: text,
        OutputFormat: 'mp3',
        VoiceId: voiceId,
        Engine: 'neural', // Use neural engine for better quality
        TextType: 'text'
      };

      const response = await this.polly.synthesizeSpeech(params).promise();

      if (response.AudioStream) {
        // AWS Polly AudioStream can be a Buffer, Uint8Array, or ArrayBuffer
        let arrayBuffer: ArrayBuffer;

        if (response.AudioStream instanceof ArrayBuffer) {
          arrayBuffer = response.AudioStream;
        } else if (response.AudioStream instanceof Uint8Array) {
          arrayBuffer = response.AudioStream.buffer.slice(
            response.AudioStream.byteOffset,
            response.AudioStream.byteOffset + response.AudioStream.byteLength
          ) as ArrayBuffer;
        } else if (typeof Buffer !== 'undefined' && response.AudioStream instanceof Buffer) {
          // Node.js Buffer
          arrayBuffer = response.AudioStream.buffer.slice(
            response.AudioStream.byteOffset,
            response.AudioStream.byteOffset + response.AudioStream.byteLength
          ) as ArrayBuffer;
        } else {
          // Fallback: try to convert to ArrayBuffer
          const data = response.AudioStream as any;
          if (data.buffer && typeof data.byteOffset === 'number' && typeof data.byteLength === 'number') {
            arrayBuffer = data.buffer.slice(
              data.byteOffset,
              data.byteOffset + data.byteLength
            ) as ArrayBuffer;
          } else if (data instanceof ArrayBuffer) {
            arrayBuffer = data;
          } else {
            console.warn('Unexpected AudioStream type:', typeof response.AudioStream);
            return null;
          }
        }

        console.log('Received audio buffer from Polly, size:', arrayBuffer.byteLength);
        return arrayBuffer;
      } else {
        console.warn('No audio stream received from Polly');
        return null;
      }
    } catch (error) {
      console.error('Polly TTS Error:', error);
      return null;
    }
  }

  async playAudio(arrayBuffer: ArrayBuffer): Promise<void> {
    // Stop any currently playing audio
    if (this.currentAudio) {
      try {
        this.currentAudio.pause();
        this.currentAudio.dispatchEvent(new Event('ended'));
      } catch (e) {
        console.warn('Error stopping current audio:', e);
      } finally {
        this.currentAudio = null;
      }
    }

    console.log('Attempting to play Polly audio buffer, size:', arrayBuffer.byteLength);

    return new Promise((resolve, reject) => {
      try {
        if (arrayBuffer.byteLength === 0) {
          console.warn('Received empty audio buffer');
          reject(new Error('Received empty audio buffer'));
          return;
        }

        const blob = new Blob([arrayBuffer], { type: 'audio/mpeg' });
        const url = URL.createObjectURL(blob);
        const audio = new Audio(url);

        this.currentAudio = audio;

        console.log('Created audio element with Polly blob URL');

        audio.onended = () => {
          if (this.currentAudio === audio) {
            this.currentAudio = null;
          }
          URL.revokeObjectURL(url);
          console.log('Polly audio playback completed');
          resolve();
        };

        audio.onerror = (error) => {
          if (this.currentAudio === audio) {
            this.currentAudio = null;
          }
          URL.revokeObjectURL(url);
          console.error('Polly audio playback error:', error);
          reject(new Error('Failed to play Polly audio'));
        };

        audio.oncanplay = () => {
          console.log('Polly audio can be played');
        };

        audio.onloadeddata = () => {
          console.log('Polly audio data loaded');
        };

        audio.onplay = () => {
          console.log('Polly audio play started');
        };

        console.log('Attempting to play Polly audio...');
        const playPromise = audio.play();

        if (playPromise !== undefined) {
          playPromise.then(() => {
            console.log('Polly audio play started successfully');
          }).catch(error => {
            if (this.currentAudio === audio) {
              this.currentAudio = null;
            }
            URL.revokeObjectURL(url);
            console.error('Polly audio play error:', error);
            if (error.name === 'AbortError') {
              console.log('Polly audio play was aborted');
              resolve();
            } else {
              reject(new Error('Failed to play Polly audio: ' + error.message));
            }
          });
        }
      } catch (error) {
        console.error('Error playing Polly audio:', error);
        if (this.currentAudio) {
          this.currentAudio = null;
        }
        reject(error);
      }
    });
  }
}
